* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
.card {
  align-items: center;
  height: 20rem;
  width: 29rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
input {
  height: 2.3rem;
  width: 11rem;
  margin: 0.5rem;
  padding-left: 0.8rem;
  border: none;
  align-items: center;
  border-radius: 0.7rem;
}
button {
  height: 2rem;
  width: 5rem;
  font-size: 1rem;
  text-decoration: solid;
  font-weight: bold;
  color: black;
  border-radius: 0.8rem;
  border: none;
  background-color: yellow;
}
.card-title {
  color: rgb(248, 1, 1);
  padding-top: 9rem;
  font-size: 2rem;
  font-weight: bold;
}

.result {
  font-size: 3rem;
  color: black;
  background-color: rgb(255, 255, 255, 0.2);
  text-shadow: 3px, 6px, rgba(50, 50, 70, 0.5);
  width: 70%;
  height: auto;
  border-radius: 1rem;
  margin-top: 1rem;
}
.image1 {
  background-image: url("https://1.bp.blogspot.com/-NUUufmmp_r4/XQMoWTviwUI/AAAAAAAAIOk/9K0IwnZpt5c9F0q6oIKyNxMDQsZPZoB-QCLcBGAs/s1600/spring-autumn-colorful-nature-magical-forest-mobile-wallpaper.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
}
.image2 {
  background-image: url("https://th.bing.com/th/id/OIP.7xGGi5WcoNYhOiSlY5WpiQHaNK?w=182&h=324&c=7&r=0&o=5&dpr=1.25&pid=1.7");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
}
.total {
  width: 100%;
}
@media screen and (max-width: 700px) {
  .card-title {
    padding-top: 15rem;
    color: red;
    font-size: 2rem;
    font-weight: bold;
  }
  .result {
    font-size: 3rem;
    color: black;
    background-color: rgb(255, 255, 255, 0.2);
    text-shadow: 3px, 6px, rgba(50, 50, 70, 0.5);
    width: 60%;
    height: auto;
    border-radius: 1rem;
    margin-top: 1rem;
  }
}
